.container-fluid {
	position: relative;
}

.logo {
	height: 3.2rem;
	padding: 0rem 1.15rem;
	pointer-events: none;

}

.header {
	position: relative;
	height: 6.25rem;
	background-color: #363c3c;
	align-items: center;
	justify-content: space-between;
	box-shadow: 0px 5px 5px rgba(0, 0, 0, 0.15);
	z-index: 1;
}

.content {
	position: relative;
	height: calc(100% - 6.25rem);
}

.sidebar {
	width: 22.2%;
	display: flex;
	flex-direction: column;
	background-color: #d6d8d8;
	border-right: 1px solid #A2A2A2;
	box-shadow: 5px 0px 5px rgba(0, 0, 0, 0.15);
}

.random_btn {
	color: #363c3c;
	background-color: #FFFFFF;
	font-size: 1.5rem;
	font-weight: 700;
	padding: 0.75rem 0rem 0.65rem 0rem;
	margin: 5rem 1.05rem 1.8rem 1.15rem;
	border: 1px solid #000000;
	border-radius: 10px;
	text-align: center;
	text-decoration: none;
	display: inline-block;
	cursor: pointer;
}

.clear_btn {
	width: 260px;
	color: #363c3c;
	background-color: #FFFFFF;
	font-size: 1.5rem;
	font-weight: 700;
	padding: 0.75rem 0rem 0.65rem 0rem;
	margin: 1rem auto;
	border: 1px solid #000000;
	border-radius: 10px;
	text-align: center;
	text-decoration: none;
	cursor: pointer;

}

.group_btn {
	color: #363c3c;
	background-color: transparent;
	border: none;
	font-size: 1.8rem;
	padding: 0rem 1rem;
	text-align: left;
	text-decoration: none;
	margin: 5px 2px;
	cursor: pointer;
	width: 150px;
}

.search_bar {
	color: #363c3c;
	background-color: #FFFFFF;
	width: 92%;
	font-size: 1.5rem;
	font-weight: 400;
	padding: .85rem 1.25rem .55rem 1.35rem;
	border: none;
	border-radius: 8px;
	text-align: left;
	text-decoration: none;
	display: inline-block;
	cursor: pointer;
}

.search_bar::placeholder {
	color: #878b8b;
}

.view {
	width: 77.8%;
	padding: 5.02rem 2.5% 0rem 2.5%;
}

.swatch {
	width: 220px;
	height: 258px;
	border: 1px solid #FFFFFF;
	margin: 0rem 0.25rem 2.6rem 0.25rem;
	border-radius: 10px;
	box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.25);
	cursor: pointer;
}

.swatch > label {
	font-size: 1.5rem;
	line-height: 50px;
	height:50px;
	width: 100%;
	margin-top: 207px;
	padding: 0rem 1.5rem;
	background-color: white;
	border-radius: 0px 0px 10px 10px;
	overflow: hidden;

}

.page {
	display: flex;
	flex-wrap: wrap;
	flex-direction: row;
	justify-content: space-between;
	align-content: flex-start;
}

.paginate {
	color: #363c3c;
	text-align: center;
	margin: 2.65rem auto 0rem auto;
	font-size: 1.5rem;
	padding-inline-start: 0rem;
	list-style: none;
}

.paginate > li {
	margin:  0rem 0.45rem;
	padding: 0rem 0.45rem;
	display: inline;
}

.paginateActive { /* not sure why the reference photo doesn't use text-decoration: underline */
	font-weight: bold;
	border-bottom: 2px solid #363c3c;
}

.paginatePrevious, .paginateNext, .paginateDisabled {
	display: none;
}

.navrow {
	display: flex;
}
